@import url(https://fonts.cdnfonts.com/css/against-2);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.typewriter-container {
    text-align: center;
}

.typewriter-text {
    font-size: 48px;
    color: lightblue;
    margin-bottom: 20px;
    padding: 10px;
    text-shadow: 10px 10px 10px 10px;
    font-family: 'against', sans-serif;
    padding: 10px;
justify-content: center;}

.text-1 {
    color: orange;
    font-size:50px;
}

.text-2 {
    color: silver;
        font-size: 50px;
}

.text-3 {
    color: white;
        font-size: 50px;
}
.mine{
    height: 40px;
    justify-content:center;
}
.whatsapp-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 60px;
    height: 60px;
    background-color: #25D366;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    z-index: 9999;
}

.whatsapp-icon {
    color: #fff;
    font-size: 30px;
}

.whatsapp-button:hover {
    background-color: darkgreen;

}
.email-button {
    position: fixed;
    bottom: 20px;
    left: 20px;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 2px 2px 6px rgba(0,0,0,0.3);
  }
  
  .email-button:hover {
background-color: goldenrod;
  }
.scroll-to-top-button {
    position: fixed;
    bottom: 20px;
    right: 50%;
    width: 50px;
    height: 50px;
    background-color: rgb(90, 158, 180);
    color: #fff;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 2px 2px 6px rgba(0,0,0,0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
  }
  .scroll-to-top-button:hover{
    background-color: orange;
  }
  
